import React from "react";
import Slider from "react-slick";
import HomeBg from '../../../images/header.png';

import Image1 from '../../../images/tool/NotesEditor/NotesEditor_1.png'

import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

import {
    BrowserRouter as Router,
    Route,
    Link
  } from "react-router-dom";

function NotesEditor() {
  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
  };
  
  return ( 
    <div>
      <div class="warkBg" style = {{backgroundImage:`url(${HomeBg})`}}>
      <div class = "contentArea">
        <div class = "workPageCard">
          <div class = "workPageBox">
            <div class = "sliderArea">
            <Slider {...settings}>
            <div>
              <div class = "videoFrame">
                <img class = "gameImage" src = {Image1}/>
              </div>
            </div>        
            </Slider>
            </div>

            <div class="info">
            <h1>NotesEditor</h1><br/>
            <h3>～説明～</h3>
            UnityEditor拡張を用いて開発した音楽ゲームの譜面作成ツールです。<br/>
            TAPとHOLDノーツ、ソフランの設定が可能。<br/>
            現在Boothで販売しています。<br/>
            <a href = "https://torizu.booth.pm/items/3137276"><h3>BOOTH</h3></a>
            <br/>
            </div>
          </div>
        </div>
      </div>
      </div>
    </div>
  );
}

export default NotesEditor;