import React from "react";
import Slider from "react-slick";
import HomeBg from '../../../images/header.png';
import Image1 from '../../../images/game/Illuminator/Illuminator.png';

import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

import {
    BrowserRouter as Router,
    Route,
    Link
  } from "react-router-dom";

function　Illuminator() {
  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
  };

  return ( 
    <div>
      <div class="warkBg" style = {{backgroundImage:`url(${HomeBg})`}}>
      <div class = "contentArea">
        <div class = "workPageCard">
          <div class = "workPageBox">
            <div class = "sliderArea">
              <Slider {...settings}>
              <div>
                <div class = "videoFrame">
                  <img class = "gameImage" src = {Image1}/>
                </div>
              </div>
              </Slider>
            </div>
            <div class="info">
            <h1>イルミネーター</h1><br/>
            <h3>～説明～</h3>
            Cluster GAMEJAM in SUMMERで開発したVR Worldです。<br/><br/>
            Clusterで遊ぶことが出来ます。<br/>
            <a href = "https://cluster.mu/w/d4400673-1628-47a6-bd9e-5c702fa65f06"><h3>CLUSTER</h3></a><br/>
            Clusterのワールドプレイイベントで取り上げていただきました。<br/>
            <a href = "https://twitter.com/cluster_jp/status/1420338245938909189"><h3>Twitter</h3></a><br/>

            ジャンル：VR World<br/>
            プラットフォーム：PC,VR<br/>
            操作：VRコントローラ,マウス＆キーボード<br/>
            </div>
          </div>
        </div>
      </div>
      </div>
    </div>
  );
}

export default Illuminator
