import React from 'react';
import Xmassy from '../images/jacket/game/Xmassy.png'
import Soundrite from '../images/jacket/game/Soundrite.png'
import BrainTraining from '../images/jacket/game/BrainTraining.png'
import Illuminator from '../images/jacket/game/Illuminator.png'
import {
  BrowserRouter as Router,
  Route,
  Link
} from "react-router-dom";
const ROUTER_BASENAME = process.env.NODE_ENV === 'development' ? '/' : '/portfolio';

function GameList() {
    return(
      <div class = "worksContentArea">
        <div  class = "contentAreaBox">
          <Link to="/game/Xmassy/">
            <div class = "workContentCard">
              <div class="workContentThmbnailWrapper">
                ::before
                <div class="workContentThmbnail" style = {{backgroundImage:`url(${Xmassy})`}}/>        
              </div>
              <div class="workContentTitle">Xmassy</div>
            </div>
          </Link>

          <Link to="/game/Soundrite/">
            <div class = "workContentCard">
              <div class="workContentThmbnailWrapper">
                ::before
                <div class="workContentThmbnail" style = {{backgroundImage:`url(${Soundrite})`}}/>        
              </div>
              <div class="workContentTitle">Soundrite</div>
            </div>
          </Link>

          <Link to="/game/BreinTraining/">
            <div class = "workContentCard">
              <div class="workContentThmbnailWrapper">
                ::before
                <div class="workContentThmbnail" style = {{backgroundImage:`url(${BrainTraining})`}}/>        
              </div>
              <div class="workContentTitle">BrainTraining</div>
            </div>
          </Link>

          <Link to="/game/Illuminator/">
            <div class = "workContentCard">
              <div class="workContentThmbnailWrapper">
                ::before
                <div class="workContentThmbnail" style = {{backgroundImage:`url(${Illuminator})`}}/>        
              </div>
              <div class="workContentTitle">イルミネーター</div>
            </div>
          </Link>
        </div>
      </div>
    );
}


export default GameList;