import React from 'react';

import NotesEditor from '../images/jacket/tool/NotesEditor.png'
import MapEditor from '../images/jacket/tool/MapEditor.png'

import {
  BrowserRouter as Router,
  Route,
  Link
} from "react-router-dom";
const ROUTER_BASENAME = process.env.NODE_ENV === 'development' ? '/' : '/portfolio';

function ToolList() {
    return(
      <div class = "worksContentArea">
        <div  class = "contentAreaBox">
          <Link to="/tool/noteseditor/">
            <div class = "workContentCard">
              <div class="workContentThmbnailWrapper">
                ::before
                <div class="workContentThmbnail" style = {{backgroundImage:`url(${NotesEditor})`}}/>        
              </div>
              <div class="workContentTitle">NotesEditor</div>
            </div>
          </Link>

          <Link to="/tool/mapeditor/">
            <div class = "workContentCard">
              <div class="workContentThmbnailWrapper">
                ::before
                <div class="workContentThmbnail" style = {{backgroundImage:`url(${MapEditor})`}}/>        
              </div>
              <div class="workContentTitle">MapEditor</div>
            </div>
          </Link>
        </div>
      </div>
    );
}


export default ToolList;