import React from "react";
import Slider from "react-slick";

import HomeBg from '../../../images/header.png';
import Image1 from '../../../images/game/BrainTraining/BrainTraining.png';

import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

import {
  BrowserRouter as Router,
  Route,
  Link
} from "react-router-dom";

function BreinTraining() {
  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
  };
  return (  
    <div>
      <div class="warkBg" style = {{backgroundImage:`url(${HomeBg})`}}>
      <div class = "contentArea">
        <div class = "workPageCard">
          <div class = "workPageBox">
            <div class = "sliderArea">
            <Slider {...settings}>
            <div>
              <div class = "videoFrame">
                <img class = "gameImage" src = {Image1}/>
              </div>
            </div>        
            </Slider>
            </div>

            <div class="info">
            <h1>BrainTraining（ブレトレ）</h1><br/>
            <h3>～説明～</h3>
            第3回全国電子工学系学校ゲーム制作コンペティションでグランプリを受賞した作品です。<br/>

            <br/>
            ジャンル：脳トレゲーム<br/>
            プラットフォーム：PC<br/>
            操作：マウスクリック<br/>
            <br/>
            </div>
          </div>
        </div>
      </div>
      </div>
    </div>
  );
}

export default BreinTraining;