import React from "react";
import Slider from "react-slick";
import HomeBg from '../../../images/header.png';

import Image1 from '../../../images/tool/MapEditor/MapEditor_1.png'
import Image2 from '../../../images/tool/MapEditor/MapEditor_2.png'

import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

import {
    BrowserRouter as Router,
    Route,
    Link
  } from "react-router-dom";

function MapEditor() {
  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
  };
  
  return ( 
    <div>
      <div class="warkBg" style = {{backgroundImage:`url(${HomeBg})`}}>
      <div class = "contentArea">
        <div class = "workPageCard">
          <div class = "workPageBox">
            <div class = "sliderArea">
            <Slider {...settings}>
            <div>
              <div class = "videoFrame">
                <img class = "gameImage" src = {Image1}/>
              </div>
            </div>
            <div>
              <div class = "videoFrame">
                <img class = "gameImage" src = {Image2}/>
              </div>
            </div>      
            </Slider>
            </div>

            <div class="info">
            <h1>MapEditor</h1><br/>
            <h3>～説明～</h3>
            Qtを用いて開発したMap作成ツール<br/>
            Unityでの開発で使いやすいように、UnityEditorとプロセス間通信でデータのやり取りが出来るようになっています。<br/>
            また、ブレゼンハムのアルゴリズムを利用して線引きを補助して操作性を上げています。<br/>
            オブジェクトやアイテム情報をデータベースに保存、保存したデータにアイコンを設定してMap上で分かりやすく出来ます。<br/>
            <br/>
            </div>
          </div>
        </div>
      </div>
      </div>
    </div>
  );
}

export default MapEditor;