import React from 'react';
import GameList from './GameList';

import Xmassy from './work/game/Xmassy';
import Soundrite from './work/game/Soundrite';
import BreinTraining from './work/game/BreinTraining';
import Illuminator from './work/game/Illuminator';
import Construction　from './work/game/Construction';

import {
  BrowserRouter as Router,
  Route,
  Link,  
  Switch,
} from "react-router-dom";
const ROUTER_BASENAME = process.env.NODE_ENV === 'development' ? '/' : '/portfolio';

function Game() {
    return(
      <div>
      <Switch>
        <Route path="/game/Xmassy/" exact component={Xmassy} />
        <Route path="/game/Soundrite/" exact component={Soundrite} />
        <Route path="/game/BreinTraining/" exact component={BreinTraining} />
        <Route path="/game/Illuminator/" exact component={Illuminator} />
        
        <Route path="/game/construction/" exact component={Construction} />
        <Route  component={null}/>
      </Switch>

        <Route  component={GameList} />
      </div>
    );  
}

export default Game;