import React from 'react';
import ToolList from './ToolList';
import NotesEditor from './work/tool/NotesEditor';
import MapEditor from './work/tool/MapEditor'
import {
  BrowserRouter as Router,
  Route,
  Link,
  Switch
} from "react-router-dom";
const ROUTER_BASENAME = process.env.NODE_ENV === 'development' ? '/' : '/portfolio';

function Tool() {

    return(
      <div>
      <Switch>
        <Route path="/tool/NotesEditor/" exact component={NotesEditor} />
        <Route path="/tool/MapEditor/" exact component={MapEditor} />
      </Switch>

      <Route  component={ToolList} />
      </div>
    );
}


export default Tool;;