import React from "react";
import Slider from "react-slick";
import Movie from "../../../movies/Soundrite.mp4"
import HomeBg from '../../../images/header.png';

import Image1 from '../../../images/game/Soundrite/Soundrite_1.png'
import Image2 from '../../../images/game/Soundrite/Soundrite_2.png'
import Image3 from '../../../images/game/Soundrite/Soundrite_3.png'
import Image4 from '../../../images/game/Soundrite/Soundrite_4.png'

import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

import {
    BrowserRouter as Router,
    Route,
    Link
  } from "react-router-dom";

function　Soundrite() {
  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
  };

  return (
    <div>
      <div class="warkBg" style = {{backgroundImage:`url(${HomeBg})`}}>
      <div class = "contentArea">
        <div class = "workPageCard">
          <div class = "workPageBox">
            <div class = "sliderArea">
              <Slider {...settings}>
              <div>
                <div class = "videoFrame">
                  <video src={Movie} controls></video>
                </div>
              </div>
              <div>
                <div class = "videoFrame">
                  <img class = "gameImage" src = {Image1}/>
                </div>
              </div>
              <div>
                <div class = "videoFrame">
                  <img class = "gameImage" src = {Image2}/>
                </div>
              </div>
              <div>
                <div class = "videoFrame">
                  <img class = "gameImage" src = {Image3}/>
                </div>
              </div>
              <div>
                <div class = "videoFrame">
                  <img class = "gameImage" src = {Image4}/>
                </div>
              </div>
              </Slider>
            </div>
            <div class="info">
            <h1>Soundrite（サウンドライト）</h1><br/>

            <h3>～説明～</h3>
            シンプルな音楽ゲームが作りたいと思い開発しました。<br/>
            譜面作成ツールから自作し、TAPとHOLD,ソフランで構成された内容になっています。<br/>
            判定タイミングの調整やサドプラ、リフト、ハイスピードなどの設定も可能になっています。<br/>
            
            <br/>
            ジャンル：音楽ゲーム<br/>
            プラットフォーム：PC<br/>
            操作：キーボード<br/><br/>

            開発人数：1人<br/>
            開発期間：約5カ月<br/>
            開発環境：VisualStudio2019<br/>
            開発言語：C++<br/>
            使用ライブラリ：dxlib、ADX2LE、boost、Crypto++<br/>
            </div>
          </div>
        </div>
      </div>
      </div>
    </div>
  );
}

export default Soundrite;