import logo from './logo.svg';
import './App.css';
import React from "react";
import {
  BrowserRouter as Router,
  Route,
  Switch,
  Link
} from "react-router-dom";
import ScrollToTop from './ScrollToTop'
import TwitterIcon from './images/icon/TwitterIcon.png';
import FacebookIcon from './images/icon/FacebookIcon.png';
import GitHubIcon from './images/icon/GitHubIcon.png';
import HamburgerOnButton from './images/ui/HamburgerOnButton.png';
import HamburgerOffButton from './images/ui/HamburgerOffButton.png';

import Home from './components/Home';
import Profile from './components/Profile';
import Game from './components/Game';
import Tool from './components/Tool';
import Contact from './components/Contact';

import Header from './components/Header';
const ROUTER_BASENAME = process.env.NODE_ENV === 'development' ? '/' : '/portfolio';

function App() {

  function HamburgerOn()
  {
    document.getElementById("HamburgerMenu").style.display = "inline";
  }
  function HamburgerOff()
  {
    document.getElementById("HamburgerMenu").style.display = "none";
  }

  window.onresize = function () {
    windowResize();
    };
 function windowResize() {
   if(document.getElementById("HamburgerMenu").style.display == "inline")
    {
      var relativeValue = document.getElementById("relativeValue");
      var style = window.getComputedStyle(relativeValue);
      var width = style.width;
      if(parseInt(width) > 760)
      {
        document.getElementById("HamburgerMenu").style.display = "none";
      }
    }
  }

  return (
    <body id = "relativeValue">
    <div>
      <div class = "page">
        <Router basename={ROUTER_BASENAME}>
        <ScrollToTop>
          <Switch>

          <Route path={["/profile/", "/game/","/tool/","/tool/","/contact/"]} exact component={Header} />
          <Route component={null} />
          </Switch>

          <Switch>
            <Route path="/" exact component={Home} />
            <Route path="/profile/" exact component={Profile} />     
            <Route path="/game/" component={Game} />
            <Route path="/tool/" component={Tool} />
            <Route path="/contact/" exact component={Contact} />
          </Switch>


        <ul id = "TopMenu" class="topnav">
          <div id ="phone">
            <button class = "MenuButton" onClick={HamburgerOn}><img class = "HamburgerButton" src={HamburgerOnButton} alt="menu"/></button>
            <li class = "center"><Link to="/"><font class = "home">nkcUgai</font></Link></li>
          </div>       
          <div id ="pc">
            <li class = "home"><Link to="/"><font class = "home">nkcUgai</font></Link></li>

            <li class="right"><Link to="/contact/">CONTACT</Link></li>
            <li class="right"><Link to="/tool/">TOOL</Link></li>
            <li class="right"><Link to="/game/">GAME</Link></li>
            <li class="right"><Link to="/profile/">PROFILE</Link></li>
          </div>
        </ul>

        <div id = "HamburgerMenu" class = "OpenMenu">
          <ul class = "OpenMenuArea">
            <button class = "MenuButton" onClick={HamburgerOff}><img class = "HamburgerButton" src={HamburgerOffButton} alt="menu"/></button>
            <li><Link to="/profile/"><font class = "OpenMenuFont">PROFILE</font></Link></li>
            <li><Link to="/game/"><font class = "OpenMenuFont">GAME</font></Link></li>
            <li><Link to="/tool/"><font class = "OpenMenuFont">TOOL</font></Link></li>
            <li><Link to="/contact/"><font class = "OpenMenuFont">CONTACT</font></Link></li>
          </ul>
        </div>

        </ScrollToTop>
        </Router>

        <div class ="bottomArea">
          <div class ="center">
            <a href = "https://twitter.com/NkcUgai"><img class = "bottomIcon"  src={TwitterIcon} /></a>
            <a href = "https://www.facebook.com/TaichiUgai"><img class = "bottomIcon"  src={FacebookIcon} /></a>
            <a href = "https://github.com/nkcUgai"><img class = "bottomIcon"  src={GitHubIcon} /></a>
          </div>
        </div>


      </div>
    </div>
    </body>

  );
}

export default App;
