import React from "react";
import Slider from "react-slick";
import Movie from "../../../movies/Xmassy.mp4"
import HomeBg from '../../../images/header.png';

import Image1 from '../../../images/game/Xmassy/Xmassy_1.png'
import Image2 from '../../../images/game/Xmassy/Xmassy_2.png'
import Image3 from '../../../images/game/Xmassy/Xmassy_3.png'
import Image4 from '../../../images/game/Xmassy/Xmassy_4.png'
import Image5 from '../../../images/game/Xmassy/Xmassy_5.png'

import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

import {
    BrowserRouter as Router,
    Route,
    Link
  } from "react-router-dom";

function Xmassy() {
  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
  };
  
  return ( 
    <div>
      <div class="warkBg" style = {{backgroundImage:`url(${HomeBg})`}}>
      <div class = "contentArea">
        <div class = "workPageCard">
          <div class = "workPageBox">
            <div class = "sliderArea">
            <Slider {...settings}>
            <div>
              <div class = "videoFrame">
                <video src={Movie} controls></video>
              </div>
            </div>
            <div>
              <div class = "videoFrame">
                <img class = "gameImage" src = {Image1}/>
              </div>
            </div>
            <div>
              <div class = "videoFrame">
                <img class = "gameImage" src = {Image2}/>
              </div>
            </div>  
            <div>
              <div class = "videoFrame">
                <img class = "gameImage" src = {Image3}/>
              </div>
            </div>  
            <div>
              <div class = "videoFrame">
                <img class = "gameImage" src = {Image4}/>
              </div>
            </div>  
            <div>
              <div class = "videoFrame">
                <img class = "gameImage" src = {Image5}/>
              </div>
            </div>      
            </Slider>
            </div>

            <div class="info">
            <h1>Xmassy</h1><br/>
            <h3>～説明～</h3>
            日本ゲーム大賞2019アマチュア部門に応募した作品です。<br/>
            第8回全国専門学校ゲームコンペティションでファイナリスト賞を受賞しました。<br/><br/>
            テーマ「☆」から、壁を走ってイルミネーションを飾り付ける新しいアクションゲームを作ってみたいという考えで開発しました。<br/><br/>

            <br/>
            ジャンル：ライティングアクションゲーム<br/>
            プラットフォーム：PC<br/>
            操作：Xboxコントローラ<br/><br/>

            開発人数：4人<br/>
            開発期間：約3カ月<br/>
            開発環境：Unity2019_4.8f1<br/>
            開発時期：専門学生1年後期～2年前期<br/>
            担当箇所：プレイヤー移動操作、イルミネーション配置、プレイヤーモデル作製、企画<br/>
            </div>
          </div>
        </div>
      </div>
      </div>
    </div>
  );
}

export default Xmassy;